    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

#about p {
    font-size: 1.1rem;
    line-height: 1.8;
    color: #073642be;
}

#about h2 {
